<template>
  <CRow>
    <CCol col="12" md="6">
      <CCard>
        <CCardHeader>
          <CIcon name="cil-justify-center" /> Notifications about stocks running
          out.
        </CCardHeader>
        <CCardBody>
          <div>
            <CAlert v-if="lowMeds.length == 0" color="success">
              <strong>Currently has 0 low stocks.</strong>
            </CAlert>
          </div>
          <div>
            <CAlert
              v-for="(med, index) in lowMeds"
              :key="index"
              show
              color="primary"
            >
              <strong>{{ med.medicine.name }}</strong> only has
              <strong>{{ med.remaining_quantity }}</strong> items left.
              <!-- <a href="#" class="alert-link">an example link</a>. -->
            </CAlert>
          </div>
        </CCardBody>
      </CCard>
    </CCol>
    <CCol col="12" md="6">
      <CCard>
        <CCardHeader>
          <CIcon name="cil-justify-center" /> Notifications about stocks
          expiring.
        </CCardHeader>
        <CCardBody>
          <div>
            <CAlert v-if="expireMeds.length == 0" color="success">
              <strong>Currently has 0 expiring stocks.</strong>
            </CAlert>
          </div>
          <div>
            <CAlert
              v-for="(med, index) in expireMeds"
              :key="index"
              show
              color="primary"
            >
              <strong>{{ med.medicine.name }}</strong> only has
              <strong>{{ getdays(med.expiry_date) }}</strong> days left to
              expire.
            </CAlert>
          </div>
        </CCardBody>
      </CCard>
    </CCol>
  </CRow>
</template>

<script>
import moment from "moment";
export default {
  name: "Alerts",
  data() {
    return {
      lowMeds: [],
      expireMeds: [],
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.$http
        .get("http://localhost:3000/notification/get")
        .then((res) => {
          this.lowMeds = res.data;
          console.log(res.data);
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
      this.$http
        .get("http://localhost:3000/notification/getExpiry")
        .then((res) => {
          this.expireMeds = res.data;
          console.log(res.data);
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
            if(err.response.status == 401) {
              this.$store.dispatch("logout").then(() => {
            });
          }
        });
    },
    getdays(date) {
      var given = moment(date);
      var current = moment().startOf("day");
      return moment.duration(given.diff(current)).days();
    },
  },
};
</script>
